.first-slide {
  height: 100vh;
  width: 100%;
  position: relative;

  @include mobile {
    height: 100dvh;
  }

  &__box {
    height: 100vh;
    display: flex;
    align-items: center;

    @include mobile {
      height: 100dvh;
    }
  }

  &__inner {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    grid-gap: 100px;
    height: fit-content;
    max-height: 950px;
    height: 100%;

    @include laptop {
      max-height: 100%;
    }

    @include small-tablet {
      justify-content: space-between;
      padding-top: 135px;
      padding-bottom: 75px;
    }

    @include mobile {
      padding-top: 9dvh;
      padding-bottom: 9.5dvh;
    }
  }
  .main-title {
    @include mobile {
      text-align: right;
    }
  }

  .main-top {
    @media (max-width: (375px)) {
      grid-gap: 10px;
    }
  }
}
